:root {
  --primary-color: #2c3e50;
  --secondary-color: #3498db;
  --accent-color: #0056b3;
  --text-color: #555;
  --light-text-color: #ecf0f1;
  --background-color: #f4f7f6;
  --card-background: #fff;
  --border-color: #ccc;
  --shadow-light: 0 2px 5px #0000001a;
  --shadow-medium: 0 4px 10px #00000014;
  --shadow-heavy: 0 4px 8px #00000026;
  --spacing-unit: 1rem;
  --box-shadow1: #007bff33;
}

* {
  box-sizing: border-box;
  font-family: Roboto, Arial, sans-serif;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  line-height: 1.6;
}

.container {
  max-width: 1200px;
  padding: 0 var(--spacing-unit);
  margin: 0 auto;
}

section {
  padding: calc(var(--spacing-unit) * 3) 0;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--primary-color);
  margin-bottom: calc(var(--spacing-unit) * .5);
}

h1 {
  font-size: 2.8em;
}

h2 {
  font-size: 2.2em;
}

p {
  color: var(--text-color);
  margin-bottom: calc(var(--spacing-unit) * 1.5);
  font-size: 1.1em;
}

ul {
  padding: 0;
  list-style: none;
}

ul li a {
  color: var(--accent-color);
  text-decoration: none;
  transition: color .3s;
}

ul li a:hover, ul li a:focus {
  color: var(--text-color);
  text-decoration: underline;
}

article {
  color: var(--text-color);
  text-align: left;
  margin-top: calc(var(--spacing-unit) * 2);
  padding: 0 calc(var(--spacing-unit) * 2);
  font-size: 1.15em;
  line-height: 1.8;
}

header {
  background-color: var(--primary-color);
  color: var(--light-text-color);
  padding: var(--spacing-unit) 0;
  box-shadow: var(--shadow-light);
  z-index: 1000;
  position: sticky;
  top: 0;
}

header .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

header .site-logo, footer .site-logo {
  width: 32px;
  height: 32px;
  margin-right: 10px;
}

header h1 {
  color: var(--light-text-color);
  align-items: center;
  margin: 0;
  display: flex;
}

nav ul {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav ul li {
  margin: 0 calc(var(--spacing-unit) * 1.5);
}

nav ul li a {
  color: var(--light-text-color);
  font-weight: bold;
  text-decoration: none;
  transition: color .3s ease-in-out;
}

nav ul li a:hover, nav ul li a:focus {
  color: var(--secondary-color);
}

nav ul li a:after {
  content: "";
  background: var(--secondary-color);
  width: 0;
  height: 2px;
  transition: width .3s;
  display: block;
}

nav ul li a:hover:after {
  width: 100%;
}

#scroll-top-btn {
  z-index: 99;
  background-color: var(--accent-color);
  color: var(--light-text-color);
  cursor: pointer;
  box-shadow: var(--shadow-light);
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 15px;
  font-size: 18px;
  transition: background-color .3s;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
}

#scroll-top-btn:hover {
  background-color: var(--text-color);
}

.menu-toggle {
  color: var(--light-text-color);
  cursor: pointer;
  font-size: 2rem;
  display: none;
}

main {
  max-width: 900px;
  margin: calc(var(--spacing-unit) * 2) auto;
  padding: calc(var(--spacing-unit) * 2);
  background-color: var(--card-background);
  box-shadow: var(--shadow-medium);
  text-align: center;
  border-radius: 8px;
}

img.rounded {
  object-fit: cover;
  border: 5px solid var(--secondary-color);
  width: 200px;
  height: 200px;
  box-shadow: var(--shadow-heavy);
  border-radius: 50%;
  transition: transform .3s ease-in-out;
}

img.rounded:hover {
  transform: scale(1.05);
}

footer {
  text-align: center;
  padding: calc(var(--spacing-unit) * 1.5) 0;
  margin-top: calc(var(--spacing-unit) * 3);
  background-color: var(--primary-color);
  font-size: .9em;
}

footer p {
  color: var(--light-text-color);
  margin-bottom: .5rem;
}

.portfolio-container {
  padding: calc(var(--spacing-unit) * 2) 0;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.portfolio-item {
  background-color: var(--card-background);
  border: 1px solid var(--border-color);
  text-align: center;
  box-shadow: var(--shadow-light);
  border-radius: 8px;
  flex: 300px;
  transition: transform .3s;
  overflow: hidden;
}

.portfolio-item:hover {
  transform: translateY(-5px);
}

.portfolio-item img {
  object-fit: cover;
  border-bottom: 1px solid var(--border-color);
  max-width: 100%;
  height: 200px;
  margin: 0 auto;
  display: block;
}

.portfolio-item h2 {
  color: #333;
  margin: 15px 0 10px;
  font-size: 1.5rem;
}

.portfolio-item p {
  color: #666;
  text-align: left;
  padding: 0 15px 15px;
  font-size: .95rem;
}

.intro-text {
  text-align: center;
  color: var(--text-color);
  margin-bottom: 40px;
  font-size: 1.1rem;
}

.contact-grid {
  gap: calc(var(--spacing-unit) * 2);
  padding-top: calc(var(--spacing-unit) * 2);
  border-top: 1px solid #eee;
  flex-wrap: wrap;
  display: flex;
}

.contact-form {
  flex: 2;
  min-width: 300px;
}

div.contact-info {
  text-align: left;
  flex: 1;
  min-width: 250px;
}

div.contact-info h2 {
  color: #333;
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.4rem;
}

div.contact-info p {
  text-align: left;
  margin-bottom: 20px;
  font-size: .95rem;
}

div.contact-info ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

div.contact-info ul li {
  margin-bottom: 10px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  color: #333;
  margin-bottom: 8px;
  font-weight: bold;
  display: block;
}

.form-group textarea {
  resize: vertical;
  min-height: 100px;
}

input, .form-group input[type="text"], .form-group input[type="email"], .form-group input[type="tel"], .form-group textarea {
  border: 1px solid var(--border-color);
  box-sizing: border-box;
  width: 100%;
  color: var(--text-color);
  border-radius: 5px;
  padding: 12px;
  font-size: 1rem;
  transition: border-color .3s;
}

input:focus, .form-group input:focus, .form-group textarea:focus {
  border-color: var(--secondary-color);
  box-shadow: 0 0 5px var(--box-shadow1);
  outline: none;
}

.btn {
  background-color: var(--accent-color);
  color: var(--light-text-color);
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 10px 20px;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.btn:hover {
  background-color: var(--text-color);
}

.submit-btn {
  background-color: var(--accent-color);
  width: 100%;
  color: var(--light-text-color);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 12px 20px;
  font-size: 1.1rem;
  font-weight: bold;
  transition: background-color .3s, transform .2s;
  display: block;
}

.submit-btn:hover {
  background-color: var(--text-color);
  transform: translateY(-2px);
}

@media screen and (width <= 768px) {
  header h1 {
    font-size: 1.5em;
  }

  header .site-logo {
    width: 18px;
    height: 18px;
  }

  nav ul {
    background-color: var(--primary-color);
    width: 100%;
    padding: var(--spacing-unit) 0;
    box-shadow: var(--shadow-light);
    flex-direction: column;
    display: none;
    position: absolute;
    top: 80px;
    left: 0;
  }

  nav ul.active {
    display: flex;
  }

  nav ul li {
    margin: var(--spacing-unit) 0;
  }

  #scroll-top-btn {
    padding: 8px;
    right: 5px;
  }

  .menu-toggle {
    display: block;
  }

  .portfolio-container {
    flex-direction: column;
    align-items: center;
  }

  .portfolio-item {
    flex: none;
    width: 90%;
  }

  .contact-grid {
    flex-direction: column;
  }

  .contact-form, div.contact-info {
    width: 100%;
    padding: 0;
  }
}

.css-valid {
  border: 0;
  width: 88px;
  height: 31px;
}
